.player {
    padding: 0;
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    overflow: hidden;
}
.player__image {
    font-size: 0;
    img {
        max-width: 72px;
        border-radius: var(--block-radius) 0 0 var(--block-radius);
    }
    @at-root {
        .player--purple & { border-right: solid 6px var(--color-purple); }
        .player--blue & { border-right: solid 6px var(--color-blue); }
        .player--green & { border-right: solid 6px var(--color-green); }
        .player--yellow & { border-right: solid 6px var(--color-yellow); }
        .player--red & { border-right: solid 6px var(--color-red); }
        .player--black & { border-right: solid 6px var(--color-black); }
    }
}
.player__content {
    width: 100%;
    height: 72px;
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    justify-content: space-between;
    border-radius: 0 var(--block-radius) var(--block-radius) 0;
    padding: var(--card-padding);
    border: var(--card-border);
    border-left: none;
    background: var(--card-background);
}
.player__info {
    margin-right: var(--card-padding);
    h2, p {
        margin: 0;
    }
    h2 {
        margin-bottom: 0.5em;
    }
}



