body {
  background: #333;
  background-size: cover;
  background-repeat: no-repeat;
  min-height: 100vh;
  margin: 0 auto;
  position: relative; }

.module {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: calc(100% - 4em); }

.card {
  background: var(--card-background);
  border: var(--card-border);
  border-radius: var(--block-radius);
  padding: var(--card-padding); }

.modal-container {
  display: none;
  align-items: center;
  justify-content: center;
  position: absolute;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  background: #111;
  padding: 1.5rem; }
  .modal-container.modal--show {
    display: flex; }

.modal {
  width: 100%;
  max-height: 100%;
  max-width: 760px;
  overflow-x: hidden;
  overflow-y: scroll;
  position: relative; }
  .modal.modal--show {
    display: block; }

.modal__close-btn {
  position: absolute;
  padding: calc(var(--card-padding) / 2);
  top: 0;
  right: 0;
  border-left: var(--card-border);
  border-bottom: var(--card-border);
  border-radius: 0 0 0 var(--block-radius);
  line-height: 0; }
  .modal__close-btn svg {
    fill: #fff;
    height: 32px;
    width: 32px; }
  .modal__close-btn:hover {
    background: rgba(255, 255, 255, 0.15);
    border: none; }

.app {
  overflow: hidden;
  display: grid;
  grid-template-columns: 100vw;
  grid-template-rows: calc(100vh - 60px) 60px;
  position: relative; }

nav {
  grid-row: 2; }

.view-container {
  grid-row: 1;
  height: calc(100vh - 60px);
  position: absolute;
  left: 0;
  display: flex;
  flex-flow: row nowrap;
  transition: all ease-in-out 0.5s; }
  .view-container.view__1--active {
    left: 0; }
  .view-container.view__2--active {
    left: -100vw; }
  .view-container.view__3--active {
    left: -200vw; }
  .view-container.view__4--active {
    left: -300vw; }
  .view-container.view__5--active {
    left: -400vw; }
  .view-container.view__6--active {
    left: -500vw; }

.view {
  padding: calc( var(--grid-margin) * 3);
  width: 100vw;
  height: 100%;
  overflow-x: hidden;
  overflow-y: scroll; }

nav ul {
  padding: 0;
  margin: 0 auto;
  list-style: none;
  width: 100%;
  display: grid;
  grid-template-rows: 60px;
  grid-auto-columns: 1fr;
  grid-auto-flow: column; }

nav button, nav a {
